import React, { useState, useEffect, useCallback, useRef } from 'react'
import { Card, Button, Input, Dropdown, ButtonGroup, Divider } from 'semantic-ui-react'
import { connect } from "react-redux"
import Head from './components/head';
import '../../../App.css';
import UNIswapList from '../../../tokenList/UNIswapList/index.json';
import { getChainInfo, isApprove } from '../../../components/global/functions';
import { Toast } from 'antd-mobile'
import web3 from "../../../ABI/web3";
import UNISwapRouterABI from "../../../ABI/UNI/UNISwapRouter.json";
import UniswapV3FactoryABI from "../../../ABI/UNI/UniswapV3Factory.json";
import UniswapV3PoolABI from "../../../ABI/UNI/UniPool.json";
import { AbiItem } from 'ethereum-multicall/dist/esm/models';
import { Contract } from 'web3-eth-contract';
import Fun from "../../../components/global/function";
import LoadingReport from './components/LoadingClose';
import { To18decimals, chackTokenBlance, TokenPrice, TokenPriceThisChain, getTokenPriceAdr } from "../../../components/global/functionTS"
import CreatConfim from './components/creatConfim';
import { CELOadr, MATICadr } from '../../../components/global/globals';
import erc20abi from '../../../ABI/ERC20.abi.json';
import LGOracleManualABI from '../../../ABI/LGOracleManual.json';
import LGoracle from '../../../ABI/LGOracle.json'

interface Token {
    LogoONOFF: boolean,
    name: string,
    adr: string,
    value: string,
    price: number,
}

interface params {
    path: string,
    recipient: string,
    deadline: number,
    amountIn: string,
    amountOutMinimum: string
}

function LockCreat(props: any) {

    const tokenList = UNIswapList.MATIC

    const slippages = [
        {
            "key": 1,
            "text": '5',
            "value": 5
        },
        {
            "key": 2,
            "text": '10',
            "value": 10
        },
        {
            "key": 3,
            "text": "15",
            "value": 15
        },
        {
            "key": 4,
            "text": "20",
            "value": 20
        },
        {
            "key": 5,
            "text": "25",
            "value": 25
        },
        {
            "key": 6,
            "text": "30",
            "value": 30
        }
    ]
    const [slippage, setSlippage] = useState<number>(10);

    const [tokenIn, setTokenIN] = useState<Token>({ LogoONOFF: true, name: 'WMATIC', adr: MATICadr.WETH, value: '', price: 0 });
    const [tokenOut, setTokenOut] = useState<Token>({ LogoONOFF: true, name: 'LG', adr: MATICadr.LG, value: '', price: 0 });

    const inPutToken = async (value: any): Promise<Token> => {
        const selectedTokenName = tokenList.find(token => token.text === value);
        const selectedTokenAdr = tokenList.find(token => token.value.toLowerCase() === value.toLowerCase());
        let token: Token = { LogoONOFF: false, name: 'unknow', adr: '', value: '', price: 0 }
        if (selectedTokenAdr) {
            token = { LogoONOFF: true, name: selectedTokenAdr.text, adr: selectedTokenAdr.value, value: '', price: 0 }
        }
        if (selectedTokenName) {
            token = { LogoONOFF: true, name: selectedTokenName.text, adr: selectedTokenName.value, value: '', price: 0 }
        }
        if (!selectedTokenName && !selectedTokenAdr) {
            try {
                const ERC20 = new web3.eth.Contract(erc20abi.abi as AbiItem[], value)
                const name = await ERC20.methods.symbol().call()
                token = { LogoONOFF: false, name: name, adr: value, value: '', price: 0 }
            } catch (error) {
                console.log(error)
                token = { LogoONOFF: true, name: 'unknow', adr: 'unknow', value: '', price: 0 }
            }
        }
        return token
    }

    const inputTokenIn = async (value: any) => {
        let result = await inPutToken(value)
        await tokenInIsApprove(value)
        setTokenIN({
            ...tokenIn,
            LogoONOFF: result.LogoONOFF,
            name: result.name,
            adr: result.adr,
            value: result.value
        })
        pathAtoB(result.adr, tokenOut.adr)
    }

    const delTokenIn = () => {
        setTokenIN({
            ...tokenIn,
            LogoONOFF: true,
            name: 'unknow',
            adr: '',
            value: ''
        })
    }

    const inputTokenOut = async (value: any) => {
        let result = await inPutToken(value)
        setTokenOut({
            ...tokenOut,
            LogoONOFF: result.LogoONOFF,
            name: result.name,
            adr: result.adr,
            value: result.value
        })
        pathAtoB(tokenIn.adr, result.adr)
    }

    const delTokenOut = () => {
        setTokenOut({
            ...tokenOut,
            LogoONOFF: true,
            name: 'unknow',
            adr: '',
            value: ''
        })
    }

    const [globalState, setGlobalState] = useState
        ({
            UNISwapRouterAdr: '',
            UNISwapRouterContract: undefined as Contract | undefined,
            UNIFactoryContract: undefined as Contract | undefined,
            tokenInisApprove: false,
            LGOracleContract:undefined as Contract | undefined,
        });

    const tokenInIsApprove = useCallback(async (tokenadr: string) => {
        const chainInfo = await getChainInfo(props.ChainID)
        const tokenIsApprove = await isApprove(tokenadr, chainInfo.ChainAdr.UNISwapRouter)
        setGlobalState(prevState => ({
            ...prevState,
            tokenInisApprove: tokenIsApprove === 'yes' ? true : false
        }))

    }, [props.ChainID])

    useEffect(() => {
        if (tokenIn.adr) {
            tokenInIsApprove(tokenIn.adr)
        }
    }, [tokenIn.adr, tokenInIsApprove])

    //加载初始化数据
    const first = useCallback(async () => {
        try {
            const chainInfo = await getChainInfo(props.ChainID)
            const _UNISwapRouterContract = new web3.eth.Contract(UNISwapRouterABI as AbiItem[], chainInfo.ChainAdr.UNISwapRouter)
            const _UNIFactoryContract = new web3.eth.Contract(UniswapV3FactoryABI as AbiItem[], chainInfo.ChainAdr.UniswapV3Factory)
            const _LGOracleContract = new web3.eth.Contract(LGoracle as AbiItem[], chainInfo.ChainAdr.LGOracle)
            setGlobalState(prevState => ({
                ...prevState,
                UNISwapRouterAdr: chainInfo.ChainAdr.UNISwapRouter,
                UNISwapRouterContract: _UNISwapRouterContract,
                UNIFactoryContract: _UNIFactoryContract,
                LGOracleContract: _LGOracleContract
            }))
            LoadingReportRef.current.loadingClose()
        } catch (error) {
            console.log(error)
        }
    }, [props.ChainID])


    useEffect(() => {
        first()
    }, [first, props.UserAdr, props.ChainID]);

    const FunRef = useRef<any>(null);
    const LoadingReportRef = useRef<any>(null);

    const Approve = async (tokenAdr: string) => {
        await FunRef.current.Approve(tokenAdr, globalState.UNISwapRouterAdr)
        tokenInIsApprove(tokenAdr)
    }

    const [advSet, setAdvSet] = useState<boolean>(false)

    const onChangeValueIn = async (value: string) => {
        setTokenIN({
            ...tokenIn,
            value: value
        })
        setTimeout(async () => {
            try {
                let priceIn = parseInt(await TokenPriceThisChain(tokenIn.adr, MATICadr.LGOracle))
                let priceOut = parseInt(await TokenPriceThisChain(tokenOut.adr, MATICadr.LGOracle))
                let valueOut = (priceIn / priceOut) * Number(value)
                setTokenIN(prevState => ({
                    ...prevState,
                    price: priceIn / 1e8
                }))
                setTokenOut({
                    ...tokenOut,
                    value: valueOut.toString(),
                    price: priceOut / 1e8
                })
            } catch (error) {
                console.log(error)
            }
        }, 500);

    }

    const onChangeValueOut = async (value: string) => {
        setTokenOut({
            ...tokenOut,
            value: value
        })
        setTimeout(async () => {
            try {
                let priceIn = parseInt(await TokenPriceThisChain(tokenIn.adr, MATICadr.LGOracle))
                let priceOut = parseInt(await TokenPriceThisChain(tokenOut.adr, MATICadr.LGOracle))
                let valueIn = (priceOut / priceIn) * Number(value)
                setTokenOut(prevState => ({
                    ...prevState,
                    price: priceOut / 1e8
                }))
                setTokenIN({
                    ...tokenIn,
                    value: valueIn.toString(),
                    price: priceIn / 1e8
                })
            } catch (error) {

            }
        }, 500)
    }

    const midToken = {
        WMTIC: '0x0d500b1d8e8ef31e21c99d1db9a6444d3adf1270',
        WBTC: '0x1BFD67037B42Cf73acF2047067bd4F2C47D9BfD6',
        WETH: '0x7ceB23fD6bC0adD59E62ac25578270cFf1b9f619',
        USDC: '0x2791Bca1f2de4661ED88A30C99A7a9449Aa84174',
        USDT: '0xc2132D05D31c914a87C6611C10748AEb04B58e8F',
    }
    const fee = [100, 500, 3000, 10000]
    const address0 = '0x0000000000000000000000000000000000000000'

    const pathAtoB = async (tokenIn: string, tokenOut: string) => {

       const res =await globalState.LGOracleContract?.methods.getTargetPool(tokenIn, tokenOut).call()
       console.log(res)
        
    }

    // const pathAtoB = async (tokenIn: string, tokenOut: string) => {
    //     console.log(tokenIn,tokenOut)
    //     let poolAdr: string = address0
    //     let tempPoolAdr: string = address0
    //     let poolLiquidity: number = 0
    //     let tempLiquidity: number
    //     tempPoolAdr = await globalState.UNIFactoryContract?.methods.getPool(tokenIn, tokenOut, fee[3]).call()
    //     console.log(tempPoolAdr)
    //     // for (let i = 0; i < fee.length; i++) {

    //     //     if (tempPoolAdr === address0) continue;
    //     //     let PoolContract = new web3.eth.Contract(UniswapV3PoolABI as AbiItem[], tempPoolAdr)
    //     //     tempLiquidity = PoolContract.methods.liquidity().call();
    //     //     if (tempLiquidity > poolLiquidity) {
    //     //         poolLiquidity = tempLiquidity;
    //     //         poolAdr = tempPoolAdr;
    //     //     }
    //     // }
    //     // if (poolAdr === address0) {
    //     //     return { isHave: false, fee: '' }
    //     // } else {
    //     //     let tempPoolContract = new web3.eth.Contract(UniswapV3PoolABI as AbiItem[], tempPoolAdr)
    //     //     let fee = tempPoolContract.methods.fee().call()
    //     //     console.log(fee)
    //     //     return { isHave: true, fee: fee }
    //     // }

    // }

    const [Params, setParams] = useState<params>({
        path: '0xe826fd888ac23396be2ec1cc19dc3a72a1c5e2ac0027100d500b1d8e8ef31e21c99d1db9a6444d3adf1270',
        recipient: '',
        deadline: 0,
        amountIn: '0',
        amountOutMinimum: '0'
    })

    const SetParams = async () => {
        const latestBlockNumber = await web3.eth.getBlockNumber();
        const latestBlock = await web3.eth.getBlock(latestBlockNumber);
        const outMinimumValue = (Number(tokenOut.value) * (100 - slippage) / 100).toString()
        console.log(outMinimumValue)
        setParams({
            ...Params,
            recipient: props.UserAdr,
            amountIn: web3.utils.toWei(tokenIn.value, 'ether'),
            deadline: Number(latestBlock.timestamp) + 600,
            amountOutMinimum: web3.utils.toWei(outMinimumValue, 'ether')
        })
    }

    const swap = async () => {
        try {
            await SetParams()
            console.log(Params)
            if (globalState.UNISwapRouterContract) {

                await globalState.UNISwapRouterContract.methods.exactInput(
                    Params
                ).send({ from: props.UserAdr, gas: '450000' })
                    .on('transactionHash', (transactionHash: string | any[]) => {
                        if (transactionHash.length !== 0) {
                            LoadingReportRef.current.loadingOpen('block', 'none', 'none', "封装中……")
                        }
                    })
                    .on('receipt', (receipt: { status: boolean; }) => {
                        if (receipt.status === true) {
                            LoadingReportRef.current.loadingOpen('none', 'block', 'none', "封装完成！通知用户 ")
                        }
                    })
                    .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                        if (receipt !== undefined && receipt.status === false) {
                            LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                        }
                    })
            }
        } catch (error) {
            console.error("Error creating lock:", error);
            // 可以添加适当的错误处理，例如显示错误提示给用户
        }
    }

    useEffect(() => { return () => { }; }, []);

    return (
        <div>
            <Head />
            <Card type="inner" style={{ width: 'inherit', minWidth: '312px', maxWidth: '500px', margin: '0px auto auto auto', backgroundColor: 'rgba(33,133,208,0.5)' }}>

                <div style={{ display: 'flex' }}>
                    <ButtonGroup color='blue' style={{ marginBottom: '3px', marginRight: '0px' }}>
                        <Button icon style={{ borderRadius: '0px 0px 0px 0px ' }}>进</Button>
                        <Button icon style={{ padding: '4px 3px 4px 3px' }}>
                            {tokenIn.LogoONOFF &&
                                <div >
                                    <img
                                        src={`${process.env.PUBLIC_URL}/logo/${tokenIn.name}.svg`}
                                        alt='auto' width='auto' height='30px'
                                    />
                                </div>
                            }
                            {!tokenIn.LogoONOFF &&
                                <div>
                                    {tokenIn.name}
                                </div>
                            }
                        </Button>
                    </ButtonGroup>

                    <Button
                        color='blue'
                        icon
                        style={{ margin: '0px 0px 3px 2px', padding: '11px 5px 11px 5px', borderRadius: '5px 0px 0px 5px ' }}
                    >
                        <Dropdown
                            options={tokenList}
                            value={tokenIn.adr}
                            onChange={(e, { value }) => inputTokenIn(value)}
                        />
                    </Button>
                    <Input
                        fluid
                        placeholder="选择或输入通证合约地址"
                        style={{ marginBottom: '3px', flex: '1', borderRadius: '0px 0px 0px 0px ' }}
                        value={tokenIn.adr}
                        onChange={(e, { value }) => inputTokenIn(value)}
                    />

                    <Button icon='trash alternate outline' onClick={delTokenIn} color='blue'
                        style={{ marginBottom: '3px', marginRight: '0px' }}
                    />
                </div>

                <div style={{ display: 'flex' }}>
                    <ButtonGroup color='blue' style={{ marginBottom: '3px', marginRight: '0px' }}>
                        <Button icon style={{ borderRadius: '0px 0px 0px 0px ' }}>进去数量</Button>
                    </ButtonGroup>

                    <Input fluid
                        value={tokenIn.value}
                        placeholder="输入数量"
                        type="number"
                        style={{ marginBottom: '3px', flex: '1' }}
                        onChange={(e, { value }) => { onChangeValueIn(value) }}
                    />

                </div>

                <div style={{ display: 'flex' }}>
                    <Button fluid size='mini'
                        color='blue' icon='long arrow alternate down'
                        style={{ margin: 'auto', width: '30px' }}
                    />
                </div>

                <div style={{ display: 'flex', marginTop: '3px' }}>
                    <ButtonGroup color='blue' style={{ marginBottom: '3px', marginRight: '0px' }}>
                        <Button icon style={{ borderRadius: '0px 0px 0px 0px ' }}>出</Button>
                        <Button icon style={{ padding: '4px 3px 4px 3px' }}>
                            {tokenOut.LogoONOFF &&
                                <div >
                                    <img
                                        src={`${process.env.PUBLIC_URL}/logo/${tokenOut.name}.svg`}
                                        alt='auto' width='auto' height='30px'
                                    />
                                </div>
                            }
                            {!tokenOut.LogoONOFF &&
                                <div>
                                    {tokenOut.name}
                                </div>
                            }
                        </Button>
                    </ButtonGroup>

                    <Button
                        color='blue'
                        icon
                        style={{ margin: '0px 0px 3px 2px', padding: '11px 5px 11px 5px', borderRadius: '5px 0px 0px 5px ' }}
                    >
                        <Dropdown
                            options={tokenList}
                            value={tokenOut.adr}
                            onChange={(e, { value }) => inputTokenOut(value)}
                        />
                    </Button>
                    <Input
                        fluid
                        placeholder="选择或输入通证合约地址"
                        style={{ marginBottom: '3px', flex: '1', borderRadius: '0px 0px 0px 0px ' }}
                        value={tokenOut.adr}
                        onChange={(e, { value }) => inputTokenOut(value)}
                    />

                    <Button icon='trash alternate outline' onClick={delTokenOut} color='blue'
                        style={{ marginBottom: '3px', marginRight: '0px' }}
                    />
                </div>


                <div style={{ display: 'flex' }}>
                    <ButtonGroup color='blue' style={{ marginBottom: '3px', marginRight: '0px' }}>
                        <Button icon style={{ borderRadius: '0px 0px 0px 0px ' }}>出来数量</Button>

                    </ButtonGroup>

                    <Input fluid
                        value={tokenOut.value}
                        placeholder="输入数量"
                        type="number"
                        style={{ marginBottom: '3px', flex: '1' }}
                        onChange={(e, { value }) => { onChangeValueOut(value) }}
                    />

                </div>

                {!globalState.tokenInisApprove && <Button fluid
                    color='teal'
                    size='tiny'
                    style={{ width: '200px', margin: 'auto', marginBottom: '3px' }}
                    onClick={() => { Approve(tokenIn.adr) }}
                >授权进的通证
                </Button>
                }
                <Button fluid
                    color='teal'
                    style={{ width: '200px', margin: 'auto' }}
                    onClick={() => { swap() }}
                >发 送
                </Button>

                <Divider horizontal onClick={() => { setAdvSet(!advSet) }} >专家设置</Divider>

                {advSet && <div style={{ display: 'flex', margin: 'auto' }}>
                    <ButtonGroup color='blue' style={{ marginBottom: '3px', marginRight: '0px' }}>
                        <Button color='blue'>滑点</Button>
                        <Button color='blue' icon
                        >
                            <Dropdown
                                options={slippages}
                                value={slippage}
                                onChange={(e, { value }) => setSlippage(value as number)}
                            />
                        </Button>
                    </ButtonGroup>
                </div>
                }

                <Card type="inner" style={{ width: 'inherit', minWidth: '312px', maxWidth: '500px', margin: '5px auto auto auto', backgroundColor: '#cce2ff' }}>
                    <div style={{ marginLeft: '10px' }}>
                        <div>
                            <b>免责声明：</b>
                            <div>&emsp;
                                本工具仅为链上交易所UniSwap的接口, 所有资产并非在本平台内。
                            </div>
                            <div>&emsp;
                                请合法合规使用该工具，如您所在地相关法律法规不允许兑换相关通证、资产、等，请立即停止使用。
                            </div>
                            <div>&emsp;
                                区块链技术属高前沿性技术，存在各种无法预知的风险,
                                本平台不承担任何原因导致的您的资产损失
                            </div>
                        </div>
                    </div>
                </Card>

            </Card>
            <Fun ref={FunRef} />
            <LoadingReport ref={LoadingReportRef} />
        </div>
    )

}

const mapStateToProps = (state: any) => {
    return {
        ChainID: state.ChainID,
        UserAdr: state.UserAdr,
        LGbalance: state.LGbalance,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        sendStore(tp: any, val: any) {
            const action = { type: tp, value: val }
            dispatch(action)
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LockCreat)



